body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background-color: #61DAFB;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #61DAFB;
    float: left;
    font-size: 80px;
    font-weight: bold;
    line-height: 150px;
    height: 150px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 150px;
    color: #4E6060;
  }
#square0 {
    border-right: 5px solid rgba(9, 127, 125, .5);
    border-bottom: 5px solid rgba(9, 127, 125, .5);
}

#square1 {
    border-right: 5px solid rgba(9, 127, 125, .5);
    border-bottom: 5px solid rgba(9, 127, 125, .5);
}

#square2 {
    border-bottom: 5px solid rgba(9, 127, 125, .5);
}

#square3 {
    border-right: 5px solid rgba(9, 127, 125, .5);
    border-bottom: 5px solid rgba(9, 127, 125, .5);
}

#square4 {
    border-right: 5px solid rgba(9, 127, 125, .5);
    border-bottom: 5px solid rgba(9, 127, 125, .5);
}

#square5 {
    border-bottom: 5px solid rgba(9, 127, 125, .5);
}


#square6 {
    border-right: 5px solid rgba(9, 127, 125, .5);
}


#square7 {
    border-right: 5px solid rgba(9, 127, 125, .5);
}


  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    align-items: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .game-info {
    margin-left: 20px;
  }
  